.image {
  width: 33%;
  height: 100%;
  position: absolute;
  background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 1),
      rgba(255, 255, 255, 0.2)
    ),
    url(https://images.unsplash.com/photo-1517999349371-c43520457b23?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8N3x8fGVufDB8fHx8&auto=format&fit=crop&w=700&q=60);
}
.colorHover {
  transition: box-shadow 0.3s;
  float: left;
}
.colorHover:hover {
  box-shadow: 0 0 8px rgba(35, 35, 35, 0.7);
}
